___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #e8f5e9;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  min-height: 100vh;
}
body header {
  background-color: #1b5e20;
  color: #ffffff;
  padding: 10px 0;
  text-align: center;
}
body .content {
  display: flex;
  flex-direction: column;
  align-self: center;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-self: stretch;
  max-width: 1200px;
  padding: 20px;
  background-color: #ffffff;
}
body .content .body {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
body .content .body .left {
  min-width: 450px;
  width: 450px;
  flex-grow: 1;
}
body .content .body .right {
  min-width: 500px;
  width: 500px;
  flex-grow: 1;
}
body table {
  align-self: center;
  width: 100%;
  max-width: 1200px;
  margin: 20px 0;
  border-collapse: collapse;
}
body table th,
body table td {
  padding: 10px;
  border: 1px solid #c8e6c9;
}
body table th {
  background-color: #0d4b10;
  color: white;
}
body table tr:nth-child(odd) {
  background-color: #f1f8e9;
}
body table #gold {
  background-color: #388e3c;
}
body table #silver {
  background-color: #81c784;
}
body table #bronze {
  background-color: #aed581;
}
body #footer {
  margin-top: auto;
  display: flex;
  flex-direction: row;
  background-color: #1b5e20;
  align-items: center;
  justify-content: center;
  justify-self: start;
  color: #ffffff;
  padding: 5px;
  text-align: center;
}
body #footer a {
  text-decoration: none;
  color: #e8f5e9;
